import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
// 
import 'vue-awesome/icons'
import Icon from 'vue-awesome/components/Icon'

//  `main .js` 
Vue.component('v-icon', Icon)

import toast from '@/components/toast/index'
import messageBox from '@/components/messageBox/index'
import message from '@/components/message/index'
import slide from '@/components/slide/slide'
import hoverTip from '@/components/hoverTip/hoverTip'
import button from '@/components/button/button'
import tabs from '@/components/tab/tabs'
import tab from '@/components/tab/tab'
import input from '@/components/input/input'
import select from '@/components/select/index'
import option from '@/components/option/index'
import radio from '@/components/radio/radio'
import radioGroup from '@/components/RadioGroup/radioGroup'
import checkBox from '@/components/checkBox/index'
import checkBoxGroup from '@/components/checkBoxGroup/index'
import dragableTop from '@/components/dragableTop/dragablepx'

Vue.use(toast);
Vue.use(messageBox);
Vue.use(message);
Vue.component('dog-slide', slide);
Vue.component('dog-hover-tip', hoverTip);
Vue.component('dog-button', button);
Vue.component('dog-tabs', tabs);
Vue.component('dog-tab', tab);
Vue.component('dog-input', input);
Vue.component('dog-select', select);
Vue.component('dog-option', option);
Vue.component('dog-radio', radio);
Vue.component('dog-radiogroup', radioGroup);
Vue.component('dog-checkBox', checkBox);
Vue.component('dog-checkBoxGroup', checkBoxGroup);
Vue.component('dog-dragableTop', dragableTop);

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: function (h) {
    return h(App)
  }
}).$mount('#app')