<template>
  <transition name="fade">
    <div class="mock" v-if="visible">
      <div :class="['message-box']">
        <h5 class="message-box-header">
          <span>{{ title }}</span>
          <span class="close-btn" @click="closeClick">
            <v-icon v-if="showClose" name="times"/>
          </span>
        </h5>
        <div class="message-box-body">
          <div class="message-icon" v-if="type !== 'alert'">
            <v-icon name="exclamation-circle" scale="1.5" color='red'/>
          </div>
          <div class="message-box-content" v-html="content"></div>
        </div>

        <div class="message-box-footer">
          <dog-button
            size="mini"
            type="primary"
            :outline="true"
            v-if="cancelButtonText && type !== 'alert'"
            @click.stop="handleClick('cancel')"
          >{{ cancelButtonText }}</dog-button>
          <dog-button
            size="mini"
            type="primary"
            v-if="confirmButtonText"
            @click.stop="handleClick('confirm')"
          >{{ confirmButtonText }}</dog-button>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import popupbox from "../mixins/popupbox";
export default {
  mixins: [popupbox],
  props: {
    type: { type: String, default: "" },
    title: { type: String, default: "" },
    content: { type: String, required: true },
    confirmButtonText: { type: String, default: "" },
    cancelButtonText: { type: String, default: "" },
    showClose: { type: Boolean, default: true }
  },
  methods: {
    handleClick(type) {
      this.$emit(type);
      this.close();
    },
    closeClick() {
      this.close();
    }
  }
};
</script>

<style lang="scss">
@import "@/assets/styles/messageBox.scss";
</style>