<template>
  <transition name="fade">
    <div
      :class="['select-wrapper', { 'is-disabled': disabled }]"
      tabindex="0"
      @click="openSelect($event)"
      @blur="handleBlur"
      @focus="handleFocus"
    >
      <div class="select-top-part">
        <template v-if="!selectItems.length">
          <span class="placeholder">{{ placeholder }}</span>
        </template>

        <template v-else>
          <div v-if="!multiple">{{ selectItems[0].label }}</div>

          <template v-else-if="!collapseTags">
            <span
              v-for="value in selectItems"
              :key="value.key"
              @click="handleDelete(value,$event)"
              class="select-tag-item c-size-s"
            >
              {{ value.label }}
              <v-icon class="delete-btn" name="times-circle" :size="8"/>
            </span>
          </template>
          <template v-else>
            <span class="select-tag-item c-size-s" @click="handleDelete(selectItems[0],$event)">
              {{ selectItems[0].label }}
              <v-icon class="delete-btn" name="times-circle" :size="8"/>
            </span>
            <span v-if="restValueNum" class="select-tag-item c-size-s c-bold">
              +
              {{ restValueNum }}
            </span>
          </template>
        </template>
      </div>

      <v-icon v-if="isOpen" class="arrow" name="chevron-up"/>

      <div class="select-bottom-part" v-show="isOpen">
        <slot></slot>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  props: {
    placeholder: { type: String, default: "" },
    optionKey: { type: String, default: "value" },
    value: { type: [String, Object, Number, Array] },
    multiple: { type: Boolean, default: false },
    collapseTags: { type: Boolean, default: false },
    disabled: { type: Boolean, default: false }
  },
  model: {
    prop: "value",
    event: "input"
  },
  data() {
    return {
      isOpen: false,
      selectValue: [],
      selectItems: []
    };
  },
  provide() {
    return {
      fatSelect: this
    };
  },
  computed: {
    restValueNum() {
      return this.selectItems.length - 1;
    }
  },
  watch: {
    value: {
      handler(value) {
        const { multiple } = this;
        const init = value ? value : multiple ? [] : "";
        this.selectValue = multiple ? [...init] : init;
      },
      immediate: true
    },
    selectValue: {
      handler(value) {
        this.selectItems = [];
      }
    }
  },
  methods: {
    openSelect(event) {
      if (event.target.localName !== "svg" && event.target.localName !== "path") {
        this.isOpen = !this.disabled && !this.isOpen;
      }
    },
    handleDelete(item, event) {
      if (event.target.localName == "svg" || event.target.localName == "path") {
        const { value } = item;
        this.selectValue = this.selectValue.filter(item => item !== value);
        this.$emit("input", this.selectValue);
        this.$emit("change", this.selectValue);
      } else {
        return;
      }
    },
    handleBlur(event) {
      this.isOpen = false;
      this.$emit("blur", event);
    },
    handleFocus(event) {
      this.$emit("focus", event);
    }
  }
};
</script>
<style lang="scss">
@import "@/assets/styles/select.scss";
</style>
