<template>
  <transition name="fade">
    <div
      :class="[`toast-${type}`, horizontalClass]"
      v-if="visible"
      :style="positionStyle"
    >
      <div class="toast-body">
        <p class="toast-title">
          {{title}}
        </p>
        <p
          class="toast-content"
          :title="content"
        >{{ content }}</p>
      </div>

      <div
        v-if="showClose"
        name="close"
        class="toast-close-btn"
        @click.stop="close"
      >
        <v-icon name="times"></v-icon>
      </div>
    </div>
  </transition>
</template>
<script>
import popupbox from "../mixins/popupbox";
import iconMap from "../mixins/iconMap";

export default {
  mixins: [popupbox],
  props: {
    // 
    type: { type: String, default: "info" },
    // 
    content: { type: String, default: "" },
    // 
    title: { type: String, default: "" },
    // 
    duration: { type: Number, default: 2500 },
    // 
    position: { type: String, default: "top-right" },
    // 
    autoClose: { type: Boolean, default: true },
    // 
    showClose: { type: Boolean, default: true },
    // Icon
    showIcon: { type: Boolean, default: false }
  },
  data() {
    return {
      closed: false,
      timer: null,
      verticalOffset: 16
    };
  },
  computed: {
    horizontalClass() {
      let position = this.position;
      return this.position.indexOf("right") !== -1 ? "right" : "left";
    },
    positionStyle() {
      return {
        ["top"]: `${this.verticalOffset}px`
      };
    },
    iconType() {
      return this.type !== "normal" ? iconMap[this.type] : this.type;
    }
  },
  watch: {
    closed(newVal) {
      if (newVal) {
        this.visible = false;
        this.$el.addEventListener("transitionend", this.destroyElement);
        this.$emit("toastClose");
      }
    }
  },
  mounted() {
    if (this.autoClose) this.startTimer();
  },
  beforeDestroy() {
    this.stopTimer();
  },
  methods: {
    startTimer() {
      if (this.duration > 0) {
        this.timer = setTimeout(() => {
          if (!this.closed) {
            this.close();
          }
        }, this.duration);
      }
    },
    stopTimer() {
      if (this.timer) clearTimeout(this.timer);
    },
    close() {
      this.closed = true;
    },
    destroyElement() {
      this.$destroy();
    }
  }
};
</script>
<style lang="scss">
@import "@/assets/styles/toast.scss";
</style>
