<template>
  <label
    :class="[
      'checkbox',
      { 'is-checked': isChecked },
      { 'is-disabled': isDisabled }
    ]"
    @click.stop="handleClick"
  >
    <span class="checkbox__label">
      <v-icon class="icon" name="check" :size="4"/>
    </span>
    
    <input v-if="false" type="checkbox" v-bind="$attrs" :value="model" @click.stop>
    <slot></slot>
  </label>
</template>

<script>
export default {
  props: {
    value: {
      type: [String, Number],
      required: true
    },
    disabled: {
      type: [Boolean],
      default: false
    },
    propValue: {
      type: [String, Number, Boolean]
    }
  },
  model: {
    prop: "propValue",
    event: "select"
  },
  data() {
    return {
      oldValueList: []
    };
  },
  computed: {
    isGroup() {
      return this.$parent.$options._componentTag === "dog-checkBoxGroup";
    },
    isDisabled() {
      return this.$parent.disabled || this.disabled;
    },
    isChecked() {
      let { isGroup, model } = this;
      if (!isGroup) return model;
      let {
        value,
        $parent: { value: selectItems }
      } = this;
      let checkLength = selectItems.length;
      if (this.$parent.min != undefined && this.$parent.max != undefined) {
        if (
          checkLength >= this.$parent.min &&
          checkLength <= this.$parent.max
        ) {
          this.oldValueList = selectItems;
          return this.oldValueList.some(item => item === value);
        } else {
          if (
            this.oldValueList.length >= this.$parent.min &&
            this.oldValueList.length <= this.$parent.max
          ) {

            selectItems = this.oldValueList;
            this.$parent.value = this.oldValueList;
            return this.oldValueList.some(item => item === value);
          }
        }
      } else {
        return selectItems.some(item => item === value);
      }
    },
    model: {
      get() {
        return this.isGroup ? this.$parent.value : this.propValue;
      },
      set(newValue) {
        const { isGroup, isChecked } = this;
        if (isGroup) {
          isChecked
            ? this.$parent.deleteItem(newValue)
            : this.$parent.selectItem(newValue);
        } else {
          this.$emit("select", newValue);
        }
      }
    }
  },
  methods: {
    handleClick(event) {
      const { isDisabled, isGroup, model, value } = this;
      if (!isDisabled) {
        this.model = isGroup ? value : !model;
      }
    }
  }
};
</script>

<style lang="scss">
@import "@/assets/styles/checkBox.scss";
</style>