<template>
  <label
    :class="[
        { 'is-checked': value === model },
        { 'is-disabled': isDisabled },
        'radio'
    ]"
    @click.stop="handleClick"
  >
    <span class="radio-inner"></span>
    <input
      class="f-hide"
      type="radio"
      :disabled="isDisabled"
      v-bind="$attrs"
      :value="model"
      @click.stop
    >
    <slot></slot>
  </label>
</template>

<script>
export default {
  props: {
    value: {
      type: [String, Number],
      required: true
    },
    disabled: {
      type: [Boolean],
      default: false
    },
    propValue: {
      type: [String, Number]
    }
  },
  model: {
    prop: "propValue",
    event: "select"
  },
  computed: {
    isGroup() {
      return this.$parent.$options._componentTag === "dog-radiogroup";
    },
    isDisabled() {
      return this.$parent.disabled || this.disabled;
    },
    model: {
      get() {
        return this.isGroup ? this.$parent.value : this.propValue;
      },
      set(newValue) {
        this.isGroup
          ? this.$parent.$emit("select", newValue)
          : this.$emit("select", newValue);
      }
    }
  },
  methods: {
    handleClick(event) {
      !this.isDisabled && (this.model = this.value);
    }
  }
};
</script>

<style lang="scss">
@import "@/assets/styles/radio.scss";
</style>