<template>
  <div class="checkbox-group" name="checkbox-group">
    <slot></slot>
  </div>
</template>
<script>
export default {
  name: "checkbox-group",
  props: {
    value: { type: Array, required: true },
    disabled: { type: Boolean },
    min: { type: [Number] },
    max: { type: [Number] }
  },
  model: {
    prop: "value",
    event: "select"
  },
  watch: {
    value(newValue) {
      this.$emit("change", newValue);
    }
  },
  computed: {
    isMin() {
      return this.value.length - 1 < this.min;
    },
    isMax() {
      return this.value.length + 1 > this.max;
    }
  },
  methods: {
    selectItem(item) {
      const { value, isMax } = this;
      if (!isMax) {
        this.$emit("select", [...value, item]);
      }
    },
    deleteItem(item) {
      const { value: selectItems, isMin } = this;
      if (!isMin) {
        this.$emit(
          "select",
          selectItems.filter(selectitem => selectitem !== item)
        );
      }
    },
  }
};
</script>
<style lang="scss">
.checkbox-group {
  display: flex;
  align-items: center;
}
</style>