<template>
  <div :class="[vertical?'slider-wrapper-vertical':'slider-wrapper', { 'is-disabled': disabled }]">
    <div v-if="!isIE" class="progress" :style="progressStyle"></div>
    <input
      :class="['slider-inner', { 'is-disabled': disabled }]"
      :disabled="disabled"
      :min="min"
      :max="max"
      type="range"
      v-on="$listeners"
      v-model="rate"
      v-bind="$attrs"
    >
    <span v-if="!isIE && showTooltip" class="tooltip" :style="toolTipPosition">{{ rate }}</span>
  </div>
</template>

<script>
export default {
  props: {
    showTooltip: { type: Boolean, default: true },
    disabled: { type: Boolean, default: false },
    min: { type: Number, default: 0 },
    max: { type: Number, default: 100 },
    value: { type: [Number, String] },
    vertical: { type: Boolean, default: false }
  },
  model: {
    prop: "value",
    event: "sliding"
  },
  data() {
    return {
      rate: 0
    };
  },
  computed: {
    isIE() {
      return (
        !!window.ActiveXObject ||
        "ActiveXObject" in window ||
        navigator.userAgent.indexOf("Edge") > -1
      );
    },
    progressStyle() {
      const { rate, max, min } = this;
      return {
        width: `${((rate - min) * 100) / (max - min)}%`
      };
    },
    toolTipPosition() {
      const { rate, max, min } = this;
      const xOffset = 9 - 18 * ((rate - min) / (max - min));
      if (!this.vertical) {
        return {
          left: `${((rate - min) * 100) / (max - min)}%`,
          marginLeft: `${xOffset}px`,
          transform: `translateX(-50%)`
        };
      } else {
        return {
          left: `${((rate - min) * 100) / (max - min)}%`,
          marginLeft: `${xOffset}px`,
          transform: `translate(-50%,-50%) rotate(90deg)`
        };
      }
    }
  },
  watch: {
    rate(value) {
      this.$emit("sliding", Number(value));
    },
    value: {
      handler(value) {
        this.rate = this.value || this.min;
      },
      immediate: true
    }
  }
};
</script>
<style lang="scss">
@import "@/assets/styles/slide.scss";
</style>