<template>
  <div
    class="dragable-block"
    :style="{'width':elWidth,'height':elHeight,'left':left,'top':top}"
    ref="div"
    @click="onBtnClicked"
  >
    <v-icon :size="6" name="chevron-up"/>
    <p>{{text}}</p>
  </div>
</template>

<script>
export default {
  name: "FloatImgBtn",
  props: {
    text: {
      type: String,
      default: "Go Top"
    },
    itemWidth: {
      type: Number,
      default: 60
    },
    itemHeight: {
      type: Number,
      default: 60
    },
    gapWidth: {
      type: Number,
      default: 10
    },
    coefficientHeight: {
      type: Number,
      default: 0.8
    },
    iconSize: {
      type: Number,
      default: 20
    },
    animationTime: {
      type: Number,
      default: 0.3
    }
  },
  data() {
    return {
      timer: null,
      currentTop: 0,
      clientWidth: 0,
      clientHeight: 0,
      left: 0,
      top: 0,
      isPC: Boolean
      // prop2data
    };
  },
  computed: {
    elWidth: function() {
      if (this.isPC) {
        return this.itemWidth + "px";
      } else {
        return (
          (this.itemWidth / document.documentElement.clientWidth) * 100 + "vw"
        );
      }
    },
    elHeight: function() {
      if (this.isPC) {
        return this.itemHeight + "px";
      } else {
        return (
          (this.itemHeight / document.documentElement.clientWidth) * 100 + "vw"
        );
      }
    },
    elGapWidth: function() {
      if (this.isPC) {
        return this.gapWidth;
      } else {
        return (this.gapWidth / document.documentElement.clientWidth) * 100;
      }
    }
  },
  watch: {},
  created() {
    this.isPC = this.IsPC();
    this.setPosition();
  },
  mounted() {
    let vm = this;
    window.addEventListener("scroll", vm.handleScrollStart);
    window.onresize = function() {
      if (vm.isPC) {
        vm.setPosition();
      } else {
        return;
      }
    };
    if (vm.isPC) {
      vm.$nextTick(() => {
        let downStamp = 0;
        let upStamp = 0;
        const div = vm.$refs.div;
        div.onmousedown = e => {
          downStamp = new Date().getTime();
          div.style.transition = "none";
          let divEvent = e || event;
          let x = divEvent.clientX - div.offsetLeft;
          let y = divEvent.clientY - div.offsetTop;
          document.onmousemove = e => {
            let divEvent = e || event;
            let l = divEvent.clientX - x;
            let t = divEvent.clientY - y;
            if (l < 0) {
              l = 0;
            } else if (
              l >
              document.documentElement.clientWidth - div.offsetWidth
            ) {
              l = document.documentElement.clientWidth - div.offsetWidth;
            }
            if (t < 0) {
              t = 0;
            } else if (
              t >
              document.documentElement.clientHeight - div.offsetHeight
            ) {
              t = document.documentElement.clientHeight - div.offsetHeight;
            }
            vm.left = l + "px";
            vm.top = t + "px";
          };
          document.onmouseup = () => {
            upStamp = new Date().getTime();
            if (upStamp - downStamp < 150) {
              window.scrollTo(0, 0);
            }
            document.onmousemove = null;
            document.onmouseup = null;
            div.style.transition = "all " + vm.animationTime + "s";
            if (parseInt(vm.left.split("px")[0]) > vm.clientWidth / 2) {
              vm.left = vm.clientWidth - vm.itemWidth - vm.gapWidth + "px";
            } else {
              vm.left = vm.gapWidth + "px";
            }
          };
          return false;
        };
        div.addEventListener("mouseup", () => {});
      });
    } else {
      vm.$nextTick(() => {
        const div = vm.$refs.div;
        div.addEventListener("touchstart", () => {
          div.style.transition = "none";
        });
        div.addEventListener(
          "touchmove",
          e => {
            let scollStopEvent = e || event;
            scollStopEvent.preventDefault();
            if (e.targetTouches.length === 1) {
              let touch = event.targetTouches[0];
              vm.left =
                ((touch.clientX - vm.itemWidth / 2) / vm.clientWidth) * 100 +
                "vw";
              vm.top =
                ((touch.clientY - vm.itemHeight / 2) / vm.clientWidth) * 100 +
                "vw";
            }
          },
          false
        );
        div.addEventListener("touchend", () => {
          div.style.transition = "all " + vm.animationTime + "s";
          if (parseInt(vm.left.split("vw")[0]) > 50) {
            vm.left =
              ((vm.clientWidth - vm.itemWidth - vm.gapWidth) / vm.clientWidth) *
                100 +
              "vw";
          } else {
            vm.left = (vm.gapWidth / vm.clientWidth) * 100 + "vw";
          }
        });
      });
    }
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.handleScrollStart);
  },
  methods: {
    setPosition() {
      if (this.isPC) {
        this.clientWidth = document.documentElement.clientWidth;
        this.clientHeight = document.documentElement.clientHeight;
        this.left = this.clientWidth - this.itemWidth - this.gapWidth + "px";
        this.top = this.clientHeight * this.coefficientHeight + "px";
      } else {
        this.clientWidth = document.documentElement.clientWidth;
        this.clientHeight = document.documentElement.clientHeight;
        this.left =
          ((this.clientWidth - this.itemWidth - this.gapWidth) /
            this.clientWidth) *
            100 +
          "vw";
        this.top =
          ((this.clientHeight * this.coefficientHeight) / this.clientWidth) *
            100 +
          "vw";
      }
    },
    // PC
    IsPC() {
      let userAgentInfo = navigator.userAgent;
      let Agents = [
        "Android",
        "iPhone",
        "SymbianOS",
        "Windows Phone",
        "iPad",
        "iPod"
      ];
      let flag = true;
      for (var v = 0; v < Agents.length; v++) {
        if (userAgentInfo.indexOf(Agents[v]) > 0) {
          flag = false;
          break;
        }
      }
      return flag;
    },
    onBtnClicked() {
      window.scrollTo(0, 0);
    },
    handleScrollStart() {
      let vm = this;
      vm.$refs.div.style.transition = "all " + vm.animationTime + "s";
      vm.timer && clearTimeout(this.timer);
      vm.timer = setTimeout(() => {
        vm.handleScrollEnd();
      }, 300);
      vm.currentTop =
        document.documentElement.scrollTop || document.body.scrollTop;

      if (vm.isPC) {
        if (parseInt(vm.left.split("px")[0]) > vm.clientWidth / 2) {
          vm.left = vm.clientWidth - vm.itemWidth / 2 + "px";
        } else {
          vm.left = -vm.itemWidth / 2 + "px";
        }
      } else {
        if (parseInt(vm.left.split("vw")[0]) > 50) {
          vm.left = 100 - ((vm.itemWidth / vm.clientWidth) * 100) / 2 + "vw";
        } else {
          vm.left = ((-vm.itemWidth / vm.clientWidth) * 100) / 2 + "vw";
        }
      }
    },
    handleScrollEnd() {
      let vm = this;
      let scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      if (scrollTop === this.currentTop) {
        if (vm.isPC) {
          if (parseInt(vm.left.split("px")[0]) > vm.clientWidth / 2) {
            vm.left = vm.clientWidth - vm.itemWidth - vm.gapWidth + "px";
          } else {
            vm.left = vm.gapWidth + "px";
          }
        } else {
          if (parseInt(vm.left.split("vw")[0]) > 50) {
            vm.left =
              ((vm.clientWidth - vm.itemWidth - vm.gapWidth) / vm.clientWidth) *
                100 +
              "vw";
          } else {
            vm.left = (vm.gapWidth / vm.clientWidth) * 100 + "vw";
          }
        }
        clearTimeout(vm.timer);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.dragable-block {
  cursor: pointer;
  background: rgb(255, 255, 255);
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.2);
  border-radius: 50%;
  color: #666666;
  z-index: 2000;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  position: fixed;


  p {
    font-size: 7px;
    margin: 0;
    letter-spacing: 1px;
  }
}
</style>
