<template>
  <div
    :class="['input-wrapper', {
    'have-prefix-icon': prefixIcon,
    'have-suffix-icon': suffixIcon
  }]"
  >
    <textarea
      v-if="type === 'textarea'"
      class="textarea-inner"
      :value="inputValue"
      v-bind="$attrs"
      v-on="inputListeners"
    />

    <template v-else>
      <v-icon v-if="prefixIcon" class="icon" :color="prefixIconColor" :name="prefixIcon"/>

      <slot name="prepend" class="input-prepend"></slot>

      <input
        :class="['input-inner', {
        'have-prepand': havePrepand,
        'have-append': haveAppend
      }]"
        :type="type"
        :value="inputValue"
        v-bind="$attrs"
        v-on="inputListeners"
      >

      <slot name="append" class="input-append"></slot>

      <v-icon v-if="suffixIcon" class="icon" :color="suffixIconColor" :name="suffixIcon"/>
    </template>
  </div>
</template>

<script>
export default {
  props: {
    type: {
      type: String,
      default: "text"
    },
    value: {
      type: [String, Number],
      default: ""
    },
    prefixIcon: {
      type: String
    },
    suffixIcon: {
      type: String
    },
    prefixIconColor: {
      type: String,
      default:'#c0c4cc'
    },
    suffixIconColor: {
      type: String,
      default:'#c0c4cc'
    }
  },
  model: {
    prop: "value",
    event: "input"
  },
  data() {
    return {
      inputValue: ""
    };
  },
  computed: {
    havePrepand() {
      return this.$slots.prepend;
    },
    haveAppend() {
      return this.$slots.append;
    },
    inputListeners() {
      return Object.assign({}, this.$listeners, {
        input: event => this.$emit("input", event.target.value)
      });
    }
  },
  watch: {
    value: {
      handler(newVal) {
        this.inputValue = newVal;
      },
      immediate: true
    }
  }
};
</script>

<style lang="scss">
@import "@/assets/styles/input.scss";
</style>