<template>
  <div :class="['hover-tip', customClass]">
    <div class="hover-part">
      <slot name="hover-part">
        <v-icon :name='iconName'></v-icon>
      </slot>
    </div>
    <div :class="[type, 'tip-part']">
      <slot name="tip-part"></slot>
      <div class="arrow"></div>
      <div class="block"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "hover-tip",
  props: {
    type: { type: String, default: "top-center" },
    customClass: { type: String, default: "" },
    iconName:{type:String,default:"question-circle"},
  }
};
</script>

<style lang="scss">
@import "@/assets/styles/hoverTip.scss";
</style>